/* Default styles for the UserProfile component */

:root {
    --primary-color: #333;
    --secondary-color: #666;
  }
  
  .UserProfile {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    max-width: 1200px;
    min-height: 100vh;
    margin: 0 auto;
  }::-webkit-scrollbar {
    display: none;
  }
  
  
  .UserProfile .details {
    min-width: 260px;
    margin-left: 40px;
    margin-right: 40px;
  }
  
  .UserProfile-header, .UserProfile-profilePhotoContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .UserProfile-profilePhotoContainer {
    width: 150px;
    height: 150px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  
  }
  
  .UserProfile-profilePhoto {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .UserProfile-username {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .UserProfile-name {
    font-size: 1.2rem;
    margin-bottom: 5px;
  }
  
  .UserProfile-bio {
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  .UserProfile-counts {
    display: flex;
    /* justify-content: center; */
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .UserProfile-count {
    margin-right: 20px;
  }
  
  .UserProfile-countNumber {
    font-weight: bold;
    text-decoration: none;
  }
  .UserProfile-countNumber:hover {
    color: var(--primary-color);
  }
  
  
  .UserProfile-countLabel {
    display: block;
    font-size: 0.8rem;
    color: #666;
  }
  
  .UserProfile-layoutToggle {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .UserProfile-layoutButton {
    font-size: 1rem;
    font-weight: bold;
    padding: 5px 10px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    margin-right: 10px;
    transition: background-color 0.3s ease-in-out;
  }
  
  .UserProfile-layoutButton.active {
    background-color: #333;
    color: #fff;
    
  }
  
  .UserProfile-posts {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .UserProfile-post {
    width: 100%;
    margin-bottom: 20px;
  }
  
  .UserProfile-post.masonry-item {
    width: 50%;
  }
  
  .UserProfile-postImage {
    width: 100%;
    height: auto;
  }
  
  /* Responsive styles for the UserProfile component */
  @media (min-width: 768px) {
    .UserProfileContainer {
      display: flex;
      justify-content: center;
    }
  
    .UserProfile {
      padding: 40px;
      flex-direction: row;
      align-items: flex-start;
  
    }
  
    .UserProfile-header {
      margin-right: 40px;
      margin-bottom: 0;
      text-align: left;
    }
  
    .UserProfile-username {
      font-size: 2rem;
    }
  
    .UserProfile-counts {
      margin-bottom: 40px;
      text-decoration: none;
    }
  
    .UserProfile-layoutToggle {
      margin-bottom: 40px;
    }
  
    .UserProfile-posts {
      width: 100%;
    }
  
    .UserProfile-post {
      width: 33.33%;
      margin-bottom: 40px;
    }
  
    .UserProfile-post.masonry-item {
      width: 100%;
    }
  }
  
  
  
  
  
  
  .UserProfile-header .logout {
    display: flex;
    align-items: center;
    color: var(--secondary-color);
    cursor: pointer;
  }
  
  .sideUserProfile-headerbar .logout svg {
    margin-right: 5px;
  }
  
  
