
.ChatUI {
    font-family: sans-serif;
    text-align: center;
    /* padding: 20px; */
    /* height: 99vh; */
    width: 100%;
    max-width: 900px;
    /* margin-left: 550px !important; */
    /* border: 5px solid red; */
    border-radius: 20px;
    /* overflow-x: hidden !important; */
    overflow: hidden;
    /* display: flex; */
    flex-direction: column;
    margin-top: 3px;
    z-index: 999999;
    position: fixed;
    max-height: 95vh;
    top:0%;
    background:var(--bg-color);
    border: 2px solid rgb(210, 210, 210);
  }
  
  .message-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* height: 300px; */
    overflow-y: scroll !important;
    overflow-x: hidden !important;
    /* margin-bottom: 20px; */
    /* padding: 10px; */
    height: 75vh;
    background:var(--bg-color);

  }
  
  .message {
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
    max-width: 60%;
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .message-text {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .message-sender {
    font-size: 12px;
    color: #aaa;
    margin-bottom: 5px;
  }
  
  .message-timestamp {
    font-size: 12px;
    color: #aaa;
    text-align: right;
  }
  
  .message.sent {
    align-self: flex-end;
    background-color: #10273f;
    color: #fff;
  }
  
  .message.received {
    align-self: flex-start;
    background-color: #685c5c;
    color: #ffffff;
  }
  
  .input-container {
    display: flex;
    align-items: center;
    margin-left: 10px;
    margin-right: 10px;
    padding-bottom: 10px;
    padding-top: 10px;
    gap: 10px;
  }
  
  .new-message-input {
    flex: 1;
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-right: 10px;
    width: 400px;
    background-color: transparent;

    
  }
  
  .send-button {
    font-size: 16px;
    padding: 10px;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
  }

  .chat-header{
    display: block;
    /* position: fixed; */
    /* background: #afdad5; */
    margin-bottom: 10px;
    padding-bottom: 15px;
    padding-top: 0px;
    /* top: 10px; */
    /* z-index: 999; */
    border-radius: 10px;
  }
  

  /* Default styles for all screen sizes */


/* Media query for desktop screens */
@media (min-width: 1200px) {
  .ChatUI {
    margin-left: 380px !important;
    margin-right: 50px !important;
    font-size: 18px;
  }
}

/* Media query for laptop screens */
@media (max-width: 1199px) and (min-width: 992px) {
  .ChatUI {
    margin-left: 350px !important;
    margin-right: 30px !important;

    font-size: 16px;
  }
}

/* Media query for tablet screens */
@media (max-width: 991px) and (min-width: 768px) {
  .ChatUI {
    /* margin-left: 265px !important; */
    margin-right: 10px !important;
    font-size: 16px;
    max-width: 90vw;
  }
}

/* Media query for mobile screens */
@media (max-width: 767px) {
  .ChatUI {
    margin-left:0px !important;
    font-size: 14px;
    max-width: 90vw;

  }
}
