.comments {
    /* min-height: 90vh;
    min-width: 450px;
     */
     /* max-height: 65vh; */
     max-height: 400px;
     overflow-y: scroll;

   }

   .comments .post-form {
    
        display: flex !important;
        flex-direction: row !important;
        justify-content: space-evenly !important;
        align-items: center !important;
        margin: 2rem auto !important;
        width: 90%;
        min-height: 0px !important;
    
   }
   .comments .comment-list {
    overflow-y: scroll;
   }

   .comments .write {
       display: flex;
       align-items: center;
       justify-content: center;
       gap: 20px;
       /* margin: 20px 0px; */
   }
   .comments .write img{
       height: 40px;
       width: 40px;
       border-radius: 50%;
       object-fit: cover;
   }
   .comments .write input{
       width: 100%;
       /* padding: 10px; */
        height: 17px;
        background: transparent;

   }

   .comments button[type="submit"] {
    /* margin-top: 1rem; */
    /* height: 30px; */

    padding: 0.5rem 1rem;
    /* font-size: 1.2rem; */
    background-color: #0077cc;
    color: white;
    border: none;
    cursor: pointer;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    transition: background-color 0.2s ease;
    margin-left: 10px;
}

       .comments .comment {
           display: flex;
           margin: 30px 0px;
           justify-content: center;
       }
           .comments .comment img{
               height: 40px;
               width: 40px;
               border-radius: 50%;
               object-fit: cover;
           }
           .comments .comment .info{
               display: flex;
               flex-direction: column;
               gap: 3px;
               align-items: flex-start;
           }
               .comments .comment .info span {
                   font-weight: 500;
               }
               .comments .comment .info p {
                margin-left: 10px;
                max-width: 500px;
               }
               .comments .comment span {
                margin-left: 10px;
            }
           .comment .date {
            flex: 1 1;
            align-self: center;
            color: gray;
            font-style: 12px;
            font-size: 13px;
           }