/* Login.css */

.login {
    margin-top: 170px;
    background-color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 100vh;
    overflow: hidden;
}

.login .card {
    display: flex;
    flex-direction: row;
    width: 80%;
    /* max-width: 800px; */
    margin: 0 auto;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
}



.login .left {
    flex: 1;
    background: url('./1.jpg');
    background-size: cover;
    padding: 50px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    color: rgb(243, 243, 243);
}

.login .left h1 {
    font-size: 100px;
}

.login .left span {
    font-size: 13px;
}

.login .left button {
    width: 50%;
    padding: 10px;
    border: none;
    background-color: #ffffff;
    color: rgb(97, 89, 104);
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
}

.login .right {
    flex: 1;
    padding: 50px;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 50px;
    justify-content: center;
}

.login .right h1 {
    color: rgb(48, 52, 55);
}

.login .right form {
    display: flex;
    flex-direction: column;
    gap: 30px;
    min-height: min-content;
}

.login .right input {
    border: none;
    border-bottom: 1.5px solid rgb(160, 160, 160);
    padding: 13px;
    background: transparent;
}


.login .right button {
    font-size: 14px;
    background: #4ab397;
    max-width: 300px !important;
    min-width: 250px !important;
    padding: 10px;
    border: none;
    font-weight: bold;
    cursor: pointer;
}
.login .right button:hover {
    background: #247962;

}

/* Media Queries */

@media screen and (max-width: 768px) {
    .login .card {
        flex-direction: column-reverse;
    }
    .login {
        max-height: 80vh;
    }

    .login .left {
        /* padding: 20px; */
        font-size: 16px;
        text-align: center;
        max-height: 20vh;
        background-image: none;
    }

    .login .left h1 {
        font-size: 60px;
        line-height: 60px;
        display: none;
    }

    .login .right {
        padding: 20px;
    }

    .login .right button {
        width: 100%;
        background: #23757b;
    }
    .login .left button {
        width: 50%;
        border-radius: 15px;
        background: #bae5d5;
    }.login .left button:hover {
        width: 50%;
        border-radius: 15px;
        background: #82c4ac;
    }

    .login .left p {
        display: none;
        color: #000;
        font-size: 14px;
    }
    .login .left span {
        color: #000;
    }
    
}

@media screen and (max-width: 480px) {
    .login .card {
        width: 90%;
    }
    .login .left {}

    .login .left h1 {
        /* font-size: 40px;
        line-height: 40px; */
        display: none;
    }

    .login .left p {
        display: none;
        /* font-size: 14px; */
    }

    

    
}
