  /* ======================================================= */

  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 2rem auto;
    width: 90%;
    max-width: 500px;
    min-height: 100vh;
  }
  
  label {
    font-size: 1.2rem;
    margin: 1rem 0;
  }
  
  input[type="text"],
  input[type="password"] {
    padding: 0.5rem;
    font-size: 1.2rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    max-width: 400px;
  }
  
  button[type="submit"] {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    font-size: 1.2rem;
    background-color: #0077cc;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s ease;
  }
  
  button[type="submit"]:hover {
    background-color: #005fa3;
  }
  
  @media only screen and (max-width: 768px) {
    input[type="text"],
    input[type="password"] {
      max-width: 300px;
    }
  }
  