.chat-list-container {
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  width: 360px;
  background-color: var(--bg-color);
  z-index: 9999;
  overflow-y: auto;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.2);
}

.chat-list-container .chat-list {
  padding: 20px;
}

.chat-list-container .chat-list .chat-list-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  cursor: pointer;
}
.chat-list-item .profile-pic img{
  margin-right: 10px;
  height:45px; 
  width:45px; 
  border-radius: 50%
}

.chat-list-container .chat-list .chat-list-item:hover {
  background-color: #f5f5f5;
}

.chat-list-container .chat-list .chat-list-item button {
  margin-right: 10px;
  border: none;
  background: transparent;
  cursor: pointer;
}

.chat-list-container .chat-list .chat-list-item-name {
  font-weight: bold;
  margin-bottom: 5px;
}

.chat-list-container .chat-list .chat-list-item-last-message {
  color: #888888;
  margin-bottom: 5px;
}

.chat-list-container .chat-list .chat-list-item-time {
  color: #888888;
}

.chat-list-container .chat-list .chat-list-item-unseen {
  display: inline-block;
  margin-left: 10px;
  padding: 5px 10px;
  border-radius: 20px;
  background-color: #f76767;
  color: #ffffff;
  font-size: 12px;
}

.chat-list-container .searchbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  padding: 0 20px;
  height: 50px;
  border-bottom: 1px solid #ebebeb;
}

.chat-list-container .searchbar input {
  border: none;
  font-size: 16px;
  width: 100%;
  padding: 10px;
}

.chat-list-container .searchbar button {
  background-color: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .chat-list-container {
    width: 100%;
  }
}
