.postForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* padding: 20px; */
    background-color: #f2f2f2;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.2);
    width: 100%;
    margin-left: 260px;
    height: 100vh;
  }
  
  .postForm form {
    margin-top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .postForm input {
    margin-bottom: 10px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .postForm button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #73a875;
    color: white;
    font-size: 16px;
    cursor: pointer;
  }
  
  .postForm button:hover {
    background-color: #446145;
  }
  
  /* .postForm img {
    margin-bottom: 10px;
    border-radius: 5px;
    
  } */
  .postForm img {
    display: block;
    margin: 0 auto 10px;
    max-width: 100%;
    height: auto;
    border-radius: 5px;
    width: 100%;
  }
  
  
  
  /* Media queries for responsive design */
  @media screen and (max-width: 768px) {
    .postForm {
      padding: 0px;
      margin-left: auto;
    }
    
    .postForm input, .postForm button {
      width: 100%;
    }
    .postForm img {
        margin-bottom: 10px;
        border-radius: 5px;
        max-height: 60vh !important;
        width: min-content;
      }
  }
  