.header {
  display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--bg-soft-color);
    /* padding: 10px 20px; */
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 0 0 10px 10px;
}
  
  .header__logo h3 {
    /* height: 40px; */
    margin: 10px;
    color: var(--text-color);
  }
  
  .header__icons {
    display: flex;
    gap: 20px;
    margin-right: 10px;
    color: var(--text-color);
  }
  
  .header__icons i {
    font-size: 24px;
  
  }
  